/* eslint-disable require-jsdoc */
const CharacterQuery = `query ($term: String, $page: Int) {
  Media(search: $term) {
    title {
      romaji
    }
    characters(sort: FAVOURITES_DESC, perPage: 50, page: $page) {
      pageInfo {
        hasNextPage
        lastPage
      }
      nodes {
        id
        name {
          full
        }
        image {
          large
        }
      }
    }
  }
}`;
export async function searchMedia(media, page = 1) {
    const resp = await fetch('https://graphql.anilist.co', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify({
            query: CharacterQuery,
            variables: {
                term: media,
                page: page,
            },
        }),
    });
    if (resp.status != 200) {
        console.error(resp.statusText);
        return;
    }
    const response = (await resp.json());
    if (response.data.Media.characters.pageInfo.hasNextPage) {
        const resp2 = await searchMedia(media, page + 1);
        response.data.Media.characters.nodes.push(...resp2.data.Media.characters.nodes);
    }
    return response;
}
