/* eslint-disable require-jsdoc */
import { pop } from "svelte-spa-router";
import { writable } from "svelte/store";
export class User {
    async pullInventory(user) {
        const resp = await fetch("https://waifuapi.karitham.dev/user/" + user);
        if (resp.status !== 200) {
            throw new Error("User not found");
        }
        try {
            const payload = (await resp.json());
            this.favorite = payload.favorite;
            this.id = payload.id;
            this.quote = payload.quote;
            this.waifus = payload.waifus;
            return this.waifus;
        }
        catch (e) {
            alert(e);
            pop();
        }
    }
}
export const Inventory = writable(new User());
